"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

export function CartaFianzaForm() {
  const [formData, setFormData] = useState({
    numeroCertificado: "",
    fechaEmision: "",
    fechaVencimiento: "",
    entidadEmisora: "",
    monto: "",
    condiciones: "",
    observaciones: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("Carta Fianza:", formData);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Carta Fianza</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <Label>N° de Certificado</Label>
              <Input
                value={formData.numeroCertificado}
                onChange={(e) => setFormData({...formData, numeroCertificado: e.target.value})}
                placeholder="CDC-2024-XXX"
              />
            </div>
            <div>
              <Label>Fecha de Emisión</Label>
              <Input
                type="date"
                value={formData.fechaEmision}
                onChange={(e) => setFormData({...formData, fechaEmision: e.target.value})}
              />
            </div>
          </div>
          
          <div className="grid grid-cols-2 gap-4">
            <div>
              <Label>Fecha de Vencimiento</Label>
              <Input
                type="date"
                value={formData.fechaVencimiento}
                onChange={(e) => setFormData({...formData, fechaVencimiento: e.target.value})}
              />
            </div>
            <div>
              <Label>Monto (S/)</Label>
              <Input
                type="number"
                value={formData.monto}
                onChange={(e) => setFormData({...formData, monto: e.target.value})}
                placeholder="0.00"
              />
            </div>
          </div>
          
          <div>
            <Label>Entidad Emisora</Label>
            <Input
              value={formData.entidadEmisora}
              onChange={(e) => setFormData({...formData, entidadEmisora: e.target.value})}
              placeholder="Ej: Banco Continental"
            />
          </div>
          
          <div>
            <Label>Condiciones</Label>
            <Textarea
              value={formData.condiciones}
              onChange={(e) => setFormData({...formData, condiciones: e.target.value})}
              placeholder="Condiciones de la carta fianza"
              rows={3}
            />
          </div>
          
          <div>
            <Label>Observaciones</Label>
            <Textarea
              value={formData.observaciones}
              onChange={(e) => setFormData({...formData, observaciones: e.target.value})}
              placeholder="Observaciones adicionales"
              rows={3}
            />
          </div>
          
          <div className="flex space-x-2">
            <Button type="submit" className="bg-[#d32f2f] hover:bg-[#b71c1c]">
              Guardar
            </Button>
            <Button type="button" variant="outline">
              Emitir Certificado
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
