"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

export function JoyasForm() {
  const [formData, setFormData] = useState({
    numeroCertificado: "",
    fechaEmision: "",
    depositante: "",
    descripcion: "",
    valorEstimado: "",
    peso: "",
    material: "",
    observaciones: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("Joyas:", formData);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Joyas</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <Label>N° de Certificado</Label>
              <Input
                value={formData.numeroCertificado}
                onChange={(e) => setFormData({...formData, numeroCertificado: e.target.value})}
                placeholder="CDC-2024-XXX"
              />
            </div>
            <div>
              <Label>Fecha de Emisión</Label>
              <Input
                type="date"
                value={formData.fechaEmision}
                onChange={(e) => setFormData({...formData, fechaEmision: e.target.value})}
              />
            </div>
          </div>
          
          <div>
            <Label>Depositante (Entidad, Despacho)</Label>
            <Input
              value={formData.depositante}
              onChange={(e) => setFormData({...formData, depositante: e.target.value})}
              placeholder="Ej: Comisaría de Miraflores"
            />
          </div>
          
          <div>
            <Label>Descripción de las Joyas</Label>
            <Textarea
              value={formData.descripcion}
              onChange={(e) => setFormData({...formData, descripcion: e.target.value})}
              placeholder="Descripción detallada de las joyas"
              rows={3}
            />
          </div>
          
          <div className="grid grid-cols-3 gap-4">
            <div>
              <Label>Valor Estimado (S/)</Label>
              <Input
                type="number"
                value={formData.valorEstimado}
                onChange={(e) => setFormData({...formData, valorEstimado: e.target.value})}
                placeholder="0.00"
              />
            </div>
            <div>
              <Label>Peso</Label>
              <Input
                value={formData.peso}
                onChange={(e) => setFormData({...formData, peso: e.target.value})}
                placeholder="Ej: 15.5 gr"
              />
            </div>
            <div>
              <Label>Material</Label>
              <Input
                value={formData.material}
                onChange={(e) => setFormData({...formData, material: e.target.value})}
                placeholder="Ej: Oro 18k"
              />
            </div>
          </div>
          
          <div>
            <Label>Observaciones</Label>
            <Textarea
              value={formData.observaciones}
              onChange={(e) => setFormData({...formData, observaciones: e.target.value})}
              placeholder="Observaciones adicionales"
              rows={3}
            />
          </div>
          
          <div className="flex space-x-2">
            <Button type="submit" className="bg-[#d32f2f] hover:bg-[#b71c1c]">
              Guardar
            </Button>
            <Button type="button" variant="outline">
              Emitir Certificado
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
